
import React,  {useState} from 'react';
import './Karticerik.css';

const Karticerik = ({kart}) => { 


    const  [goster, setGoster] = useState(true)

    const metinGoster = () => {
         if(goster == true){
             setGoster(!true);
         }
         else{ 
             setGoster(true);
         }
    }

 return(<>
        <h1> soru {kart.id}</h1>
        <p>  {kart.soru}</p>
        <p style={{ display : `${goster ?"none" :"block"}`}} >{kart.cevap}</p>
        <button onClick={metinGoster}  className={goster ?"btn btn-success" :"btn btn-danger"}>
            {goster ?"Cevabı Göster" :"Cevabı Kapat"}
        </button>

        </>
    )
}
export default Karticerik;